.ez-signinemail1, .ez-signupemail1 {
  .btn-confirm {
    /* Add modern button styles here */
    display: inline-block;
    padding: 10px 20px;
    font-size: 1em;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }

  .btn-confirm.inactive {
    /* Add styles for inactive button here */
    background-color: #ccc;
    cursor: not-allowed;
  }
  .btn-confirm.horizontal {
    /* Add styles for horizontal button here */
    display: inline-block;
    padding: 10px 20px;
    font-size: 1em;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }
}
